.Star_star__127bW {
        position: absolute;
        right: -15%;
        top: 5%;
        display: inline-block;
        width: 0;
        height: 0;
        cursor: pointer;
        margin-left: 0.9em;
        margin-right: 0.9em;
        margin-bottom: 1.2em;
        border-right: 0.3em solid transparent;
        border-bottom: 0.7em solid #eee;
        border-left: 0.3em solid transparent;
        z-index: 3;
        /* Controlls the size of the stars. */
        font-size: 1.2rem;
        transition: all 0.3s;
}
.Star_star__127bW::before,
.Star_star__127bW::after {
        content: '';
        display: block;
        width: 0;
        height: 0;
        position: absolute;
        top: 0.6em;
        left: -1em;
        border-right: 1em solid transparent;
        border-bottom: 0.7em solid #eee;
        border-left: 1em solid transparent;
        transition: all 0.3s;
        transform: rotate(-35deg);
}

.Star_star__127bW::after {
        transform: rotate(35deg);
}

.Star_star__127bW:hover,
.Star_starActive__2s52q {
